import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { TableData, ColEditDelete } from 'components/common';
import { BtnAddNew } from 'components/common/button';
import { Card, Row, Col, message } from 'antd';
import { getCertificateTypes, deleteCertificateType } from 'services/certificateType';
import { Link } from 'react-router-dom';
import routeConstant from 'routes/route/routeConstant';

const ROUTER_FORM_DATA = routeConstant.DANH_MUC__CHUNG_CHI__LOAI_CHUNG_CHI__NHAP_DU_LIEU;
class ListAllCertificatesPage extends Component {
  state = {
    reload: 0,
  };

  handleDelete = async id => {
    await deleteCertificateType(id);
    message.success('Xa thnh cng');
    this.setState({
      reload: this.state.reload + 1,
    });
  };

  render() {
    const columns = [
      {
        key: '1',
        title: 'K hiu',
        dataIndex: 'id',
        align: 'center',
      },
      {
        key: '2',
        title: 'Tn chng ch',
        dataIndex: 'label',
        align: 'center',
      },
      {
        title: 'Thao tc',
        dataIndex: 'operation',
        align: 'center',
        render: (text, record) => (
          <ColEditDelete
            linkEdit={{
              to: {
                pathname: ROUTER_FORM_DATA,
                search: `?id=${record.id}`,
              },
            }}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];

    return (
      <Card title="Cc loi chng ch">
        <Row>
          <Col span={24}>
            <Link to={ROUTER_FORM_DATA}>
              <BtnAddNew />
            </Link>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData reload={this.state.reload} service={getCertificateTypes} columns={columns} />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListAllCertificatesPage />
  </Page>
);



